<template>
  <div>
      <Spinner
        :class="{ 'fade-out': !isLoading }"
      />
      <UserDetailEdit
        :initial-profile="profile" 
        @after-submit="handleAfterSubmit"
        :is-processing="isProcessing"
      />
  </div>
</template>

<script>
import UserDetailEdit from '../components/UserDetaiEdit'
import Spinner from '../components/Spinner'
import usersAPI from '../apis/users'
import { Toast } from '../utils/helpers'


export default {
  name: 'ProfileEdit',
  components: {
    UserDetailEdit,
    Spinner,
  },
  data() {
    return {
      profile: {
        id: '',
        account: '',
        name: '',
        email: '',
        phone: '',
        address: '',
        avatar: '',
        cover: '',
        introduction: '',
      },
      isProcessing: false,
      isLoading: false
    }
  },
  methods: {
    async fetchData(id) {
      try {
        this.isLoading = true
        const { data } = await usersAPI.getUser({ id })
        this.profile = data.profile
        this.isLoading = false
      } catch (err) {
        console.log(err)
        this.isLoading = false
        this.toastFire('error', err || '取得使用者資訊失敗，請檢查連線狀態或稍後再試')
      }
    },
    async handleAfterSubmit(formData) {
      try {
        this.isProcessing = true
        const id = this.profile.id
        const { data } = await usersAPI.editUser({ id, formData })
        if (data.status !== 'success') {
          this.isProcessing = false
          this.toastFire('error', data.message)
          throw new Error(data.message)
        }
        this.toastFire('success', data.message)
        this.fetchData(id)
        this.isProcessing = false
        this.$router.push({ name: 'profile', params: { id }})
      } catch (err) {
        console.log(err)
        this.isProcessing = false
         this.toastFire('error', err || '取得使用者資訊失敗，請檢查連線狀態或稍後再試')
      }
    },
    toastFire(action,text){
      Toast.fire({
        icon: action,
        title: text,
        background: '#eee',
        showConfirmButton: false
      })
    }
  },
  beforeRouteUpdate(to, from, next) {
    const { id } = to.params
    this.fetchData(id)
    next()
  },
  created() {
    const { id } = this.$route.params
    this.fetchData(id)
  },
}
</script>

<style scoped>

.profile__block {
  margin: 260px 0 0 0;
  position: relative;
}
</style>
