<template>
  <form 
  id="targetForm"
  @submit.prevent.stop="handleSubmit">

    <div class="user__cover edit" :style="{ 'background-image': 'url(' + profile.cover + ')' }">
        <input type="file" class="custom-file-input image__input" name="cover" id="cover" @change="handleFileChange">
        <label class="custom-file-label image__label" for="cover">
            <i class="fas fa-camera"></i>
        </label>
    </div>

    <div class="row shadow card profile__block mb-5">

      <div class="col-md-4 mt-3 profile__avatar">
        <figure class="figure">
          <img
            :src="profile.avatar"
            alt="user_avatar"
            class="figure-img img-fluid rounded img__box mxw__avatar rounded-circle"
          />
          <input
            type="file"
            class="custom-file-input image__input"
            name="avatar"
            id="avatar"
            @change="handleFileChange"
          />

          <label class="custom-file-label image__label" for="avatar">
            <i class="fas fa-camera"></i>
          </label>

          <figcaption class="figure-caption"></figcaption>
        </figure>
      </div>

      <div class="row p-3 m-3 mr-1 ml-1 col-8">
        <div class="col-md-8 mt-5 pl-4">
          <div class="row mt-4"></div>

          <div class="form-group">
            <label for="name">
              <i class="fas fa-signature mr-2"></i>
              顯示名稱
            </label>
            <input
              type="text"
              class="form-control"
              name="name"
              id="name"
              placeholder="Enter User Name"
              :value="profile.name"
              spellcheck="false"
            />
          </div>

          <div class="form-group">
            <label for="account">
              <i class="fas fa-user-circle mr-2"></i>
              帳號
            </label>
            <input
              type="text"
              class="form-control"
              name="account"
              id="account"
              placeholder="Enter User Name"
              :value="profile.account"
            />
          </div>

          <div class="form-group">
            <label for="email">
              <i class="fas fa-envelope mr-2"></i>
              E-mail
            </label>
            <input
              type="text"
              class="form-control"
              name="email"
              id="email"
              placeholder="Enter User E-mail"
              :value="profile.email"
            />
          </div>

          <div class="form-group">
            <label for="phone">
              <i class="fas fa-phone-alt mr-2"></i>
              連絡電話
            </label>
            <input
              type="number"
              class="form-control inputer"
              name="phone"
              id="phone"
              placeholder="phone number"
              :value="profile.phone"
            />
          </div>

          <div class="form-group">
            <label for="address">
              <i class="fas fa-map-marker-alt mr-2"></i>
              寄送地址
            </label>
            <input
              type="text"
              class="form-control"
              name="address"
              id="address"
              placeholder="address"
              :value="profile.address"
            />
          </div>

          <div class="form-group">
            <label for="introduction">
              <i class="fas fa-envelope-open-text mr-2"></i>
              Introduction
            </label>
            <textarea
              type="text"
              class="form-control inputer textarea"
              placeholder="type your own introduction..."
              name="introduction"
              v-model="profile.introduction"
              id="introduction"
            ></textarea>
          </div>

        </div>
      </div>

      <div class="profile__control__box row col-8 justify-content-end">
        <button 
          type="submit" 
          class="btn btn-primary"
          :disabled="isProcessing"
        >
          <i class="fas fa-save text-light"></i>
          {{ isProcessing ? '處理中...' : '儲存' }}
        </button>
      </div>

    </div>

  </form>
</template>

<script>

export default {
  name: 'UserDetailEdit',
  props: {
    initialProfile: {
      type: Object,
      default: () => {
        return {
          account: '',
          name: '',
          email: '',
          phone: '',
          address: '',
          avatar: '',
          cover: '',
          introduction: '',
        }
      }
    },
    isProcessing: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      profile: {
        ...this.initialProfile,
      },
    }
  },
  methods: {
    handleFileChange(e) {
      const { files } = e.target
      const name = e.target.name
      if (!files.length) {
        this.profile[name] = this.initialProfile[name]
        return
      }
      this.profile[name] = window.URL.createObjectURL(files[0])
    },
    handleSubmit(e){
      const formData = new FormData(e.target)
      this.$emit('after-submit', formData)
    }
  },
  watch: {
    initialProfile (newValue) {
      this.profile = {
        ...this.profile,
        ...newValue
      }
    }
  }
}
</script>

<style scoped>
.scoper {
  overflow: hidden;
}
.user__cover{
  z-index: 0;
}

</style>
